import * as React from "react";
import { useForm } from "react-hook-form";

import saveBrowserState from "../../../utilities/saveBrowserState";

import Layout from "../components/layout";
import { Seo } from "../components/seo";

import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";


function Login() {
  const { register, handleSubmit, } = useForm();

  const email = "nick.golovenkin@yandex.ru";
  const user = "Д.А. Головенкин";

  const onSubmit = (application) => {
       console.log(application);
    saveBrowserState('econolabs', { application: application });
 //   setTimeout(window.location.reload(), 10000);
  };


  return <Layout>

    <div className="container my-5">

      <Container>
        <Form onSubmit={handleSubmit(onSubmit)} className="p-3">
          <Form.Group className="mb-3" controlId="formEmail">
            <Form.Label>Email</Form.Label>
            <Form.Control type="email" placeholder={!!email && email.length > 6 ? email : "email"}  {...register("email")} required />
            <Form.Text className="text-muted">
              Пароль НЕ требуется
            </Form.Text>
          </Form.Group>

          <Form.Group className="mb-3" controlId="formUser">
            <Form.Label>ФИО Группа</Form.Label>
            <Form.Control type="text" placeholder={!!user && user.length > 6 ? user : "ФИО Группа"}  {...register("user")} required />
          </Form.Group>

          <Button variant="outline-secondary" size="sm" type="submit">
            Сохранить
          </Button>
        </Form>
      </Container>

    </div>

  </Layout>
}

export default Login

export const Head = () => (
  <Seo title="Login" />
)