import * as React from "react";
import PropTypes from "prop-types";
import { Link } from "gatsby";
import { useSelector } from 'react-redux';

import { selectCaseArray, selectCaseType } from "../features/case/caseSlice"

const isActive = ({ isCurrent }) => {
  return isCurrent ? { className: "nav-link active" } : { className: "nav-link" }
}

const ExactNavLink = props => (
  <Link getProps={isActive} {...props} />
)

function Navbar({ siteTitle }) {
  const caseType = useSelector(selectCaseType) || "";
  const caseArray = useSelector(selectCaseArray) || [];


  return <nav className="navbar navbar-expand-md navbar-dark bg-secondary">
    <div className="container-fluid">
      <Link to="/" className="navbar-brand" href="#">{siteTitle}</Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#main-navbar"
        aria-controls="main-navbar" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse" id="main-navbar">
        <ul className="navbar-nav me-auto mb-2 mb-md-0">
          <li className="nav-item">
            <ExactNavLink
              to="/login"
            >
              Login
            </ExactNavLink>
          </li>

          <li className="nav-item">
            <ExactNavLink
              to="/accountingwithprofitscash"
            >
              Accounting With Profits Cash
            </ExactNavLink>
          </li>

          {Array.isArray(caseArray) & caseArray.length > 0 & caseType.includes("accounting") ?
            <li className="nav-item">
              <ExactNavLink
                to="/accountingdnd"
              >
                Accounting DnD
              </ExactNavLink>
            </li>
            : null
          }

          <li className="nav-item">
            <ExactNavLink
              to="/myworkbookdnd"
            >
              Workbook DnD
            </ExactNavLink>
          </li>


          {Array.isArray(caseArray) & caseArray.length > 0 & caseType.includes("myworkbook") ?
            <li className="nav-item">
              <ExactNavLink
                to="/myworkbook"
              >
                Workbook
              </ExactNavLink>
            </li>
            : null
          }

          {Array.isArray(caseArray) & caseArray.length > 0 & caseType.includes("myworkbook") ?
            <li className="nav-item">
              <ExactNavLink
                to="/myworkbooktrash"
              >
                Workbook trash
              </ExactNavLink>
            </li>
            : null
          }

        </ul>
      </div>

      <a href="/">
        <img
          src="https://sun9-37.userapi.com/c317630/v317630439/76a0/Bz6QTfBog0I.jpg?ava=1"
          alt=""
          style={{
            marginRight: "1rem",
            width: '40px',
            height: '40px',
            borderRadius: '50%',
            filter: 'grayscale(100%)',
            objectFit: 'cover'
          }}
        />
      </a>

    </div>
  </nav>

}

Navbar.propTypes = {
  siteTitle: PropTypes.string,
}

Navbar.defaultProps = {
  siteTitle: ``,
}

export default Navbar
